/* GLOBAL */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  background: #000;
  color: #FFD700;
  overflow-x: hidden;
}

/* WRAPPER */
.user-wrapper {
  display: flex;
  width: 100%;
  min-height: 100vh;
  background: #000;
}

/* MAIN PANEL */
.main-panel {
  flex: 1;
  padding: 2rem;
  background: #0b0b0b;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

/* TOPBAR */
.topbar {
  background: #1a1a1a;
  color: #FFD700;
  padding: 1.2rem 2rem;
  border-radius: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 15px rgba(255, 215, 0, 0.3);
  flex-wrap: wrap;
  gap: 1rem;
}

.topbar h1 {
  font-size: 1.8rem;
  font-weight: 700;
}

.user-welcome {
  margin-top: 6px;
}

.username {
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
}

/* LOGOUT BUTTON */
.logout-btn {
  background: #FFD700;
  color: #000;
  border: none;
  padding: 0.6rem 1.2rem;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.logout-btn:hover {
  background: #e6c200;
}

/* STATS */
.stats-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.2rem;
}

.stat-card {
  background: #111;
  border: 1px solid #222;
  border-radius: 16px;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  color: #FFD700;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: 0px 6px 16px rgba(255, 215, 0, 0.25);
}

.stat-card-icon {
  background: #FFD700;
  color: #000;
  font-size: 22px;
  padding: 14px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.stat-card h3 {
  font-size: 1rem;
  margin-bottom: 5px;
}

.stat-card p,
.count-up {
  font-size: 1.3rem;
  font-weight: 700;
  color: #fff;
}

.happy-count {
  font-size: 0.95rem;
  font-weight: 500;
  color: #bbb;
}

/* BOTTOM */
.bottom-dashboard {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
}

.notifications {
  flex: 1;
  background: #111;
  border: 1px solid #222;
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
  min-width: 260px;
}

.notifications h3 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid #FFD700;
  padding-bottom: 0.6rem;
}

.notifications ul {
  list-style: none;
  max-height: 250px;
  overflow-y: auto;
}

.notifications li {
  background: #1a1a1a;
  margin-bottom: 0.8rem;
  padding: 0.8rem 1rem;
  border-radius: 8px;
  font-size: 0.9rem;
  color: #FFD700;
  transition: background 0.3s ease;
}

.notifications li:hover {
  background: #222;
}

/* ===== MOBILE: TABLETS & PHONES ===== */
@media (max-width: 768px) {
  .user-wrapper {
    flex-direction: column; /* Sidebar + main stacked */
  }

  .main-panel {
    padding: 1.2rem;
    gap: 1.5rem;
  }

  .topbar {
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
    padding: 1rem;
  }

  .topbar h1 {
    font-size: 1.5rem;
  }

  .username {
    font-size: 1.1rem;
  }

  .logout-btn {
    width: 100%;
    justify-content: center;
  }

  .stats-container {
    grid-template-columns: 1fr; /* Single column */
    gap: 1rem;
  }

  .stat-card {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.8rem;
    padding: 1.2rem;
  }

  .stat-card-icon {
    font-size: 20px;
    padding: 12px;
  }

  .stat-card h3 {
    font-size: 0.95rem;
  }

  .stat-card p,
  .count-up {
    font-size: 1.2rem;
  }

  .happy-count {
    font-size: 0.85rem;
  }

  .bottom-dashboard {
    flex-direction: column;
    gap: 1rem;
  }

  .notifications {
    width: 100%;
    min-width: unset;
  }
}

/* ===== MOBILE: SMALL PHONES ===== */
@media (max-width: 480px) {
  .main-panel {
    padding: 1rem;
  }

  .topbar h1 {
    font-size: 1.3rem;
  }

  .username {
    font-size: 1rem;
  }

  .logout-btn {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
  }

  .stat-card {
    padding: 1rem;
    gap: 0.6rem;
  }

  .stat-card h3 {
    font-size: 0.9rem;
  }

  .stat-card p,
  .count-up {
    font-size: 1.05rem;
  }

  .happy-count {
    font-size: 0.8rem;
  }

  .notifications h3 {
    font-size: 1rem;
  }

  .notifications li {
    font-size: 0.85rem;
    padding: 0.7rem 0.8rem;
  }
}
.logout-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.logout-popup {
  background: #fff;
  padding: 20px 30px;
  border-radius: 12px;
  text-align: center;
  width: 320px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.3);
}

.logout-popup h3 {
  margin-bottom: 20px;
  font-size: 18px;
}

.logout-buttons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.confirm-btn {
  background: #ff4d4d;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.cancel-btn {
  background: #ddd;
  color: #000;
  padding: 10px 15px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}
