/* FAQ Main Section */
.faq-main-container {
    background-color: #000000; /* pure black */
    min-height: 200vh;
    width: 100%;
    padding: 0;
    margin: 0;
    height: 350vh;
}

/* Heading Container */
.faq-1-container {
    width: 75vw;
    text-align: center;
    /* position: fixed; */
    padding-top: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 2px solid white; */
    /* gap: 50px; */
}
.money{
margin-left: 40%;
}
/* Main Heading */
.faq-1-heading {
    font-size: 4rem;
    font-family: 'Times New Roman', Times, serif;
    font-weight: bolder;
    background: linear-gradient(90deg, #ffd700, #b28c00, #ffd700); /* Gold gradient */
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

/* Sub Heading */
.faq-2-container {
    font-size: 2.8rem;
    font-family: 'Times New Roman', Times, serif;
    font-weight: bolder;
    background: linear-gradient(90deg, #ffd700, #b28c00);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 8%;
}

/* Box Main Container */
.faq-box-container {
    width: 80vw;
    margin: 10vh auto 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
}

/* Each Box */
.faq-box-container1 {
    position: relative;
    height: 54vh;
    width: 25vw;
    background-color: #0e0e0e;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 5px 20px rgba(255, 215, 0, 0.3); /* golden glow */
    padding: 20px 15px;
    transition: 0.4s ease-in-out;
    cursor: pointer;
    border: 2px solid #ffd700;
}

.faq-box-container1:hover {
    height: 60vh;
}

/* Overlay Image */
.faq-overlay-img {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
    width: 90px;
    height: 90px;
    object-fit: contain;
    z-index: 2;
    transition: 0.4s ease-in-out;
}

.faq-box-container1:hover .faq-overlay-img {
    opacity: 0;
    transform: translateX(-50%) scale(1.1);
}

/* Hover Effect for Box */
.faq-box-container1::before {
    content: "";
    position: absolute;
    top: -50%;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, #b28c00, #ffd700, #b28c00);
    transform: skewY(345deg);
    transition: 0.5s;
    z-index: 0;
}

.faq-box-container1:hover::before {
    top: -70%;
}

/* Content Inside Box */
.faq-box-container1-content {
    position: relative;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    text-align: center;
    color: #fff;
    padding-top: 15px;
    transition: 0.4s ease-in-out;
}

.faq-box-container1:hover .faq-box-container1-content {
    opacity: 1;
    visibility: visible;
    margin-top: 20px;
}

.faq-box-container1-content h3 {
    font-size: 1.8rem;
    color: #ffd700;
    margin-top: 10%;
}

.faq-box-container1-content p {
    font-size: 1rem;
    color: #e0e0e0;
    margin-top: 10%;
}

/* =================== QNA Section Start =================== */

.qna-div {
    padding: 20px;
    margin-top: 40px;
    border-radius: 10px;
    background-color: #111111;
    color: #ffd700;
    font-size: 1.4rem;
    margin-left: 7%;
    margin-right: 60px;
    box-shadow: 0px 3px 8px rgba(255, 215, 0, 0.3);
    display: flex;
    flex-direction: column;
}

/* QNA Single Question Container */
.details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px auto;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s ease;
}

.details:hover {
    background-color: rgba(255, 215, 0, 0.08);
}

/* Question Heading */
.details h3 {
    margin: 0;
    font-size: 1.5rem;
    color: #ffd700;
}

/* Dropdown Icon */
.details span {
    font-size: 1.8rem;
    color: #ffd700;
    transform: rotate(0deg);
    transition: 0.3s ease;
}

/* Rotate Dropdown Icon when Active */
.details.active span {
    transform: rotate(180deg);
}

/* Answer Container */
.faq-answer {
    margin: 10px auto;
    padding: 10px 20px;
    width: 65%;
    color: #f8f8f8;
    font-size: 1.2rem;
    line-height: 1.7;
    background-color: rgba(255, 215, 0, 0.07);
    border-radius: 5px;
}

/* =================== QNA Section End =================== */
.faq-question {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.faq-icon {
    transition: transform 0.3s ease;
    font-size: 20px;
    color: #ffd700;
}

.faq-icon.rotate {
    transform: rotate(180deg);
}
/* ===== Mobile and Tablet Responsive FAQ CSS ===== */
@media (max-width: 1024px) {
  /* FAQ Main Container height adjustment */
  .faq-main-container {
    height: auto;
    min-height: 100vh;
    padding: 20px 10px;
  }

  /* Heading container width and padding */
  .faq-1-container {
    width: 90vw;
    padding-top: 15%;
  }

  .money {
    margin-left: 0;
  }

  /* Main Heading font size */
  .faq-1-heading {
    font-size: 2.8rem;
  }

  /* Sub Heading */
  .faq-2-container {
    font-size: 2rem;
    margin-top: 6%;
  }

  /* Box Container: Stack boxes vertically and full width */
  .faq-box-container {
    width: 95vw;
    flex-direction: column;
    gap: 20px;
    margin: 5vh auto 0 auto;
  }

  /* Each Box: Full width, adjusted height */
  .faq-box-container1 {
    width: 100%;
    height: auto;
    padding: 25px 20px;
  }

  /* Overlay image smaller and repositioned */
  .faq-overlay-img {
    top: 15%;
    width: 70px;
    height: 70px;
  }

  /* Content inside box - font size and spacing */
  .faq-box-container1-content h3 {
    font-size: 1.5rem;
    margin-top: 15%;
  }

  .faq-box-container1-content p {
    font-size: 0.95rem;
    margin-top: 15%;
  }

  /* Hover height effect disabled for smooth mobile UX */
  .faq-box-container1:hover {
    height: auto;
  }

  /* QNA Section adjustments */
  .qna-div {
    width: 95vw;
    margin-left: auto;
    margin-right: auto;
    font-size: 1.1rem;
    padding: 15px;
  }

  /* Details container: vertical stacking */
  .details {
    flex-direction: column;
    align-items: flex-start;
    margin: 15px 0;
  }

  .details h3 {
    font-size: 1.3rem;
    margin-bottom: 8px;
  }

  /* Dropdown icon smaller and repositioned */
  .details span {
    font-size: 1.5rem;
    align-self: flex-end;
  }

  /* FAQ answer width to full */
  .faq-answer {
    width: 100%;
    font-size: 1rem;
    padding: 10px 15px;
  }

  /* FAQ Question flex tweaks */
  .faq-question {
    flex-direction: column;
    align-items: flex-start;
  }

  .faq-icon {
    font-size: 18px;
  }
}

/* ===== Mobile (small phones) further tweaks ===== */
@media (max-width: 480px) {
  .faq-1-heading {
    font-size: 2rem;
    padding: 0 10px;
  }

  .faq-2-container {
    font-size: 1.6rem;
  }

  .faq-box-container1-content h3 {
    font-size: 1.2rem;
  }

  .faq-box-container1-content p {
    font-size: 0.85rem;
  }

  .qna-div {
    font-size: 1rem;
    padding: 10px;
  }

  .details h3 {
    font-size: 1.1rem;
  }

  .faq-answer {
    font-size: 0.9rem;
  }
}

@media (max-width: 1024px) {
  .faq-1-container {
    width: 90vw;
    padding-top: 15%;
    display: block;
    text-align: center;
  }

  .money {
    margin-left: 0;
    margin-top: 20px;
    width: 60vw;
    max-width: 250px;
    height: auto;
  }
}

@media (max-width: 480px) {
  .money {
    width: 80vw;
    max-width: 180px;
    margin-top: 15px;
  }
}
