/* Registration.css - SkyFunded Golden Theme */

.sky-funded-register-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
  padding: 40px 20px;
  min-height: 120vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  /* border: 2px solid gold; */
}
.todiv{
  border: 2px solid gold;
  display: flex;
  flex-direction: row;
}

/* Left Panel */
.sky-funded-register-left {
  background-color: #0e0e0e;
  color: #fff;
  padding: 40px;
  flex: 1;
  max-width: 400px;
  border-radius: 10px 0 0 10px;
  text-align: center;
  /* border: 2px solid #ffd700; */
}

.sky-funded-register-left h2 {
  font-size: 28px;
  color: #ffd700;
  margin-bottom: 15px;
}

.sky-funded-register-left p {
  font-size: 14px;
  color: #ccc;
  margin-bottom: 25px;
  line-height: 1.6;
}

.sky-funded-register-left img {
  max-width: 100%;
  height: auto;
}

/* Right Panel */
.sky-funded-register-right {
  background-color: #111;
  padding: 40px;
  flex: 1;
  max-width: 600px;
  border-radius: 0 10px 10px 0;
  box-shadow: 0 4px 20px rgba(255, 215, 0, 0.15);
  /* border: 2px solid #ffd700; */
}

.sky-funded-logo {
  color: #ffd700;
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
}

.sky-funded-register-form {
  width: 100%;
}

/* Form Fields */
.sky-funded-form-row {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.sky-funded-input-group {
  position: relative;
  flex: 1;
}

.sky-funded-input-group .icon {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  color: #ffd700;
  font-size: 16px;
}

.sky-funded-input-group input,
.sky-funded-input-group select {
  width: 100%;
  padding: 10px 10px 10px 35px;
  border: 1px solid #ffd700;
  background-color: #1a1a1a;
  color: #fff;
  border-radius: 6px;
  outline: none;
  font-size: 14px;
}

.sky-funded-input-group input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.sky-funded-input-group select {
  appearance: none;
  background-color: #1a1a1a;
  color: #fff;
}

/* Password Eye Icon */
.password-toggle-icon {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  font-size: 18px;
  color: #ffd700;
  transition: color 0.2s ease-in-out;
}

.password-toggle-icon:hover {
  color: #b28c00;
}

/* Checkbox */
.sky-funded-checkbox {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 15px 0;
  font-size: 14px;
  color: #ccc;
}

/* Register Button */
.sky-funded-register-btn {
  width: 100%;
  background-color: #ffd700;
  color: #000;
  padding: 12px;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s ease;
  margin-top: 10px;
  font-weight: bold;
  box-shadow: 0 3px 10px rgba(255, 215, 0, 0.25);
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.sky-funded-register-btn:hover {
  background-color: #b28c00;
  color: #fff;
}

/* Login Link */
.sky-funded-login-link {
  margin-top: 15px;
  text-align: center;
  font-size: 14px;
  color: #ccc;
}

.sky-funded-login-link span {
  color: #ffd700;
  cursor: pointer;
  font-weight: 600;
}

.sky-funded-login-link span:hover {
  color: #b28c00;
  text-decoration: underline;
}

/*  Toastify Custom Styling */
.Toastify__toast-theme--light {
  background-color: #111 !important;
  color: #ffd700 !important;
  border: 1px solid #ffd700;
  font-weight: bold;
}

.Toastify__progress-bar--success {
  background: #ffd700 !important;
}

.Toastify__toast-icon {
  color: #ffd700 !important;
}

/* Mobile View */
@media (max-width: 768px) {
  .sky-funded-register-container {
    flex-direction: column;
    padding: 20px 10px;
    min-height: auto;
  }

  .todiv {
    flex-direction: column;
    border: none;
  }

  /* Left Panel */
  .sky-funded-register-left {
    max-width: 100%;
    border-radius: 10px 10px 0 0;
    padding: 20px 15px;
  }

  .sky-funded-register-left h2 {
    font-size: 22px;
  }

  .sky-funded-register-left p {
    font-size: 13px;
    margin-bottom: 15px;
  }

  /* Right Panel */
  .sky-funded-register-right {
    max-width: 100%;
    border-radius: 0 0 10px 10px;
    padding: 20px 15px;
  }

  .sky-funded-logo {
    font-size: 22px;
    margin-bottom: 20px;
  }

  /* Form Layout */
  .sky-funded-form-row {
    flex-direction: column;
    gap: 12px;
  }

  .sky-funded-input-group input,
  .sky-funded-input-group select {
    font-size: 13px;
    padding: 8px 8px 8px 32px;
  }

  .sky-funded-input-group .icon {
    font-size: 14px;
    left: 8px;
  }

  /* Checkbox */
  .sky-funded-checkbox {
    font-size: 12px;
    gap: 6px;
  }

  /* Button */
  .sky-funded-register-btn {
    font-size: 14px;
    padding: 10px;
    border-radius: 5px;
  }

  /* Login Link */
  .sky-funded-login-link {
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .sky-funded-register-left h2 {
    font-size: 20px;
  }

  .sky-funded-logo {
    font-size: 20px;
  }

  .sky-funded-register-btn {
    font-size: 13px;
    padding: 8px;
  }
}
/* Password Field Container */
.password-group {
  position: relative;
}

/* Toggle Eye Icon (Password Show/Hide) */
.toggle-password {
  position: absolute;
  top: 50%;
  right: 10px;   /*  right side me */
  transform: translateY(-50%);
  cursor: pointer;
  color: #ffd700;   /* Golden Theme */
  font-size: 18px;
  transition: 0.3s ease;
}

.toggle-password:hover {
  color: #b28c00;
}
